$("#btn").click(function() {
    event.preventDefault();
    if ($('.request').valid()) {
        var dataForm = $(".request").serializeArray();
        var path = window.location.href;
        var path = path+'php/mail.php';
        console.log(dataForm);
        console.log(path);
        $.ajax({
            url: path,
            type: 'POST',
            data: dataForm,
            //dataType: 'JSON',
            success: function(data) {
                console.log(data);
            }
        });
    } else {
        console.log('Game over');
    }
});
